.main-navigation {
  @media only screen and (max-width: 580px) {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100vh;
    padding-left: 20px;
    background-color: $color-dark;
    z-index: 100;
    // Hide nav
    visibility: hidden;
    opacity: 0;
    transform: translateY(-10px);
    transition: all .2s ease-in;
  }
  &.active{
    opacity: 1;
    visibility: initial;
    transform: translateY(0px);
  }

  ul {
    display: flex;
    @media only screen and (max-width: 580px) {
      flex-direction: column;
    }

    li {
      &:not(:last-child) {
        margin-right: 40px;
        @media only screen and (max-width: 580px) {
          margin: 0 0 30px 0;
        }
      }

      a {
        &:link,
        &:visited {
          color: $color-gray;
          font-size: 20px;
          text-transform: capitalize;
          text-decoration: none;
          transition: color .2s ease-in;
          @media only screen and (max-width: 580px) {
            font-size: 24px;
          }
        }
        &:hover {
          color: $color-main;
        }
        &:focus {
          outline: 2px solid yellow;
          outline-offset: 4px;
        }
        &:active {
            outline: none;
            color: $color-main;
        }
      }
    }
  }
}


.nav-icon {
    position: relative;
    display: none;
    z-index: 101;
    &:focus,
    &:active {
        outline: none;
    }


    @media only screen and (max-width: 580px) {
    display: block;
    }
    &__btn {
        position: relative;
        border: none;
        background: transparent;
        &:focus,
        &:active {
            outline: none;
        }
    }
}

/* nav-icon-1 */
.nav-icon-1{
    width: 30px;
    height: 30px;
    position: relative;
    transition: .1s;
    margin: 10px 10px;
    cursor: pointer;
    display: inline-block;
}
.nav-icon-1 span{
    width: 5px;
    height: 5px;
    background-color: #fff;
    display: block;
    border-radius: 50%;
    position: absolute;

}
.nav-icon-1:hover span{
    transform: scale(1.2);
    transition: 350ms cubic-bezier(.8, .5, .2, 1.4);
}
.nav-icon-1 span:nth-child(1){
    left: 0;
    top: 0;
}
.nav-icon-1 span:nth-child(2){
    left: 12px;
    top: 0;
}
.nav-icon-1 span:nth-child(3){
    right: 0;
    top: 0;
}
.nav-icon-1 span:nth-child(4){
    left: 0;
    top: 12px;
}
.nav-icon-1 span:nth-child(5){
    position: absolute;
    left: 12px;
    top: 12px;
}
.nav-icon-1 span:nth-child(6){
    right: 0px;
    top: 12px;
}
.nav-icon-1 span:nth-child(7){
    left: 0px;
    bottom: 0px;
}
.nav-icon-1 span:nth-child(8){
    position: absolute;
    left: 12px;
    bottom: 0px;
}
.nav-icon-1 span:nth-child(9){
    right: 0px;
    bottom: 0px;
}
.nav-icon-1.open{
    transform: rotate(180deg);
    cursor: pointer;
    transition: .2s cubic-bezier(.8, .5, .2, 1.4);
}
.nav-icon-1.open span{
    border-radius: 50%;
    transition-delay: 200ms;
    transition: .5s cubic-bezier(.8, .5, .2, 1.4);
}
.nav-icon-1.open span:nth-child(2) {
    left: 6px;
    top: 6px;
}
.nav-icon-1.open span:nth-child(4) {
    left: 6px;
    top: 18px;
}
.nav-icon-1.open span:nth-child(6) {
    right: 6px;
    top: 6px;
}
.nav-icon-1.open span:nth-child(8) {
    left: 18px;
    bottom: 6px;
}
